import React, { useState, useEffect } from 'react';
import { Modal } from 'react-bootstrap';
import '../../components/schedule/react-scheduler/kendo-scheduler.css'

import styles from './TitleExclusion.module.css';
import { eyeIcon, eyeSlashIcon, uploadFileIcon, downloadFileIcon } from '../../assets/index';
import IndefiniteCalendarIcon from '../../assets/icons/IndefiniteCalendar.svg';
import DefiniteCalendarIcon from '../../assets/icons/DefinedCalendar.svg'
import BannerComponent from '../_reusable/banner/BannerComponent';
import Button from '../../shared/components/button/Button';
import DualTable from '../../shared/components/dual-table/DualTable';
import TitleExclusionService from './TitleExclusionService';
import TitleExclusionStorage from './TitleExclusionStorage';
import { danger, success } from '../../shared/elements/notification/NotifyComponent';
import { useCallbackPrompt } from '../_reusable/blocker/UsePromptComponent.ts';
import ProfileService from '../profile/ProfileService';
import AuthService from '../../core/auth.service';
import ProgramDetailsComponent from '../prioritisation/prioritisation-program-details/PrioritisationProgramDetailComponent';
import ImportProgramDetailsComponent from '../prioritisation/import-program-details/ImportProgramDetailsComponent';
import ShowSetTitleExclusionDateModalComponent from './modals/ShowSetTitleExclusionDateModalComponent';
import DateService from '../_reusable/javascript/date.service';
import SelectComponent from '../../shared/elements/select/select.component';
import PrioritisationService from '../prioritisation/PrioritisationService';
import ConfirmLeavePageModalComponent from '../title-group/modals/confirm-leave-page-modal/ConfirmLeavePageModalComponent';
import ImportModal from '../../shared/components/import-modal/ImportModal';
import ImportTitleToExcludeResultModal from './modals/import-title-to-exclude-result-modal/ImportTitleToExcludeResultModal';
import moment from 'moment';
import OverrideExcludedTitlesModal from './modals/override-execluded-titles-modal/OverrideExcludedTitlesModal';
import ConfirmExportModal from './modals/confirm-export-modal/ConfirmExportModal';
import ConfirmImportModal from './modals/confirm-import-modal/ConfirmImportModal';

function TitleExclusionComponent() {
  const tableOneHeader = TitleExclusionStorage.tableOneHeaders;
  const tableOneImportHeader = TitleExclusionStorage.tableOneImportHeaders;
  const tableTwoHeader = TitleExclusionStorage.tableTwoHeaders;
  const [
    isExcludedTitlesInitialLoaded,
    setIsExcludedTitlesInitialLoaded,
  ] = useState(false);
  const [isFetchingTitles, setIsFetchingTitles] = useState(true);
  const [isFetchingExcludedTitles, setIsFetchingExcludedTitles] = useState(true);
  const [tableOneData, setTableOneData] = useState([]);
  const [tableTwoData, setTableTwoData] = useState([]);
  const [viewedItemId, setViewedItemId] = useState(null);
  const [viewedItem, setViewedItem] = useState(null);
  const [queryParameters, setQueryParameters] = useState({
    sortOrder: 1,
    sortBy: 1,
  });
  const [sortParameters, setSortParameters] = useState({ order: 1, sort: 1 });
  const [tableParameters, setTableParameters] = useState({});
  const [paginationParameters, setPaginationParameters] = useState({});
  const [tablesHeight, setTablesHeight] = useState(['371px', '371px']);
  const tableLabels = ['Programme titles:', 'Excluded programme titles:'];
  const [verbiage, setVerbiage] = useState([
    { show: true, message: 'Please select a channel to list its available programme titles' },
    { show: false, message: 'Please select and add programme titles to exclude' },
  ]);
  const [shouldTableOneScrollToView, setShouldTableOneScrollToView] = useState(
    false
  );
  const [shouldTableTwoScrollToView, setShouldTableTwoScrollToView] = useState(
    false
  );

  const [selectedProgrammeTitles, setSelectedProgrammeTitles] = useState([]);
  const [titleSource, setTitleSource] = useState();
  const [shouldLoadTableOne, setShouldLoadTableOne] = useState(true);
  const [excludeTitleFormType, setExcludeTitleFormType] = useState('Create');
  const [isSaving, setIsSaving] = useState(false);
  const [isDirty, setIsDirty] = useState(false);
  const [showSetDateModal, setShowSetDateModal] = useState(false);
  const [
    showPrompt,
    confirmNavigation,
    cancelNavigation,
    hidePrompt,
  ] = useCallbackPrompt(isDirty);
  const [showConfirmLeaveModal, setShowConfirmLeaveModal] = useState(false);
  const [shouldClearSelected, setShouldClearSelected] = useState(false);
  const [channels, setChannels] = useState([]);
  const [selectedChannel, setSelectedChannel] = useState({});
  const [nextChannel, setNextChannel] = useState({});
  const [showTitleToExcludeModal, setShowTitleToExcludeModal] = useState(false);
  const [isImporting, setIsImporting] = useState(false);
  const [isOverwriting, setIsOverwriting] = useState(false);
  const [isOverriding, setIsOverriding] = useState(false);
  const [importedTitlesToExclude, setImportedTitlesToExclude] = useState([]);
  const [showImportTitlesToExcludeModal, setShowImportTitlesToExclude] = useState(false);
  const [storedSelectedFile, setStoredSelectedFile] = useState();
  const [storedImportActionType, setStoredImportActionType] = useState();
  const [showOverrideExcludedTitlesModal, setShowOverrideExcludedTitlesModal] = useState(false);
  const [isExporting, setIsExporting] = useState(false);
  const [showConfirmExportModal, setShowConfirmExportModal] =  useState(false);
  const [showConfirmImportModal, setShowConfirmImportModal] =  useState(false);

  const handleGetUser = () => {
    const { userId } = AuthService.getUserIdentity();

    ProfileService.getUser(userId).then((response) => {
      setTitleSource(response.data.organisationTitleSource);
    });
  };

  const constructQueryString = (parameters) => {
    const pagination = `?page=${
      parameters.pageNumber ? parameters.pageNumber : 1
    }&size=${parameters.pageSize ? parameters.pageSize : 50}`;

    const titleName =
      parameters.titleNameFilter && parameters.titleNameFilterValue
        ? `&titleNameFilter=${parameters.titleNameFilter}&titleNameFilterValue=${parameters.titleNameFilterValue}`
        : '';

    const genre =
      parameters.genreFilter && parameters.genreFilterValue
        ? `&genreFilter=${parameters.genreFilter}&genreFilterValue=${parameters.genreFilterValue}`
        : '';

    const grade =
      parameters.gradeFilter && parameters.gradeFilterValue
        ? `&gradeFilter=${parameters.gradeFilter}&gradeFilterValue=${parameters.gradeFilterValue}`
        : '';

    const certificate =
      parameters.certificateFilter && parameters.certificateFilterValue
        ? `&certificateFilter=${parameters.certificateFilter}&certificateFilterValue=${parameters.certificateFilterValue}`
        : '';

    const year =
      parameters.yearFilter && parameters.yearFilterValue
        ? `&yearFilter=${parameters.yearFilter}&yearFilterValue=${parameters.yearFilterValue}`
        : '';

    const searchKey = parameters.searchKey
      ? `&searchKey=${parameters.searchKey}`
      : '';

    const sort =
      parameters.sortOrder && parameters.sortBy
        ? `&columnSort=${parameters.sortBy}&sortBy=${parameters.sortOrder}`
        : '';

    const titleIds =
      tableTwoData.length > 0
        ? `&titleIds=${tableTwoData.map((title) => title.titleId).join(',')}`
        : '';

    const channelId = selectedChannel.channelId
      ? `&channelId=${selectedChannel.channelId}`
      : '';

    let queryString = pagination
      + titleName
      + genre
      + grade
      + certificate
      + year
      + searchKey
      +sort
      + titleIds
      + channelId;

    if (queryString.includes('+')) {
      queryString = queryString.replace('+', '%2B');
    }

    return queryString;
  };

  const handleSetTableParameters = (parameters) => {
    setTableParameters({
      titleNameFilter: parameters.titleNameFilter,
      titleNameFilterValue: parameters.titleNameFilterValue,
      genreFilter: parameters.genreFilter,
      genreFilterValue: parameters.genreFilterValue,
      gradeFilter: parameters.gradeFilter,
      gradeFilterValue: parameters.gradeFilterValue,
      certificateFilter: parameters.certificateFilter,
      certificateFilterValue: parameters.certificateFilterValue,
      yearFilter: parameters.yearFilter,
      yearFilterValue: parameters.yearFilterValue,
    });
  };

  const constructProgrammeTitles = (data) => {
    let programmeTitles = [];

    if (titleSource === 2) {
      programmeTitles = data.map((item) => ({
        titleId: item.titleId,
        titleName: item.titleName,
        duration: item.overrodeValues.duration || item.duration,
        action: {
          id: item.titleId,
          icon: eyeIcon,
          toolTip: 'View title details',
          actionName: 'viewTitleDetailsFromTableOne',
        },
        
        isHighlighted: item.overrode,
      }));
    } else {
      programmeTitles = data.map((item) => ({
        titleId: item.titleId,
        titleName: item.titleName,
        genre: item.overrodeValues.genre || item.genre,
        duration: item.overrodeValues.duration || item.duration,
        grade: item.overrodeValues.grade || item.grade,
        certificate: item.overrodeValues.certificate || item.certificate,
        year: item.overrodeValues.year || item.year,
        action: {
          id: item.titleId,
          icon: eyeIcon,
          toolTip: 'View title details',
          actionName: 'viewTitleDetailsFromTableOne',
        },
        isHighlighted: item.overrode,
      }));
    }

    setTableOneData(programmeTitles);
  };

  const checkIfDatesAreDefinite = (item) => {
    let state = true;
    for (let i = 0; i < item.length; i++) {
      if(!item[i].endDate) {
        state = false;
        break;
      }
    }
    return state;
  };

  const constructExcludedTitles = (data) => {
    const excludedTitles = data?.map((item) => {
      return ({
        titleId: item.titleId,
        titleName: item.titleName,
        action2:{
          id: item.titleId,
          icon: checkIfDatesAreDefinite(item.effectivityDates) ? DefiniteCalendarIcon : IndefiniteCalendarIcon,
          toolTip: item.effectivityDates.map((item) => (
            (item.startDate ? DateService.toLocalDMYFormat(item.startDate) : 'Immediately')
            + " - "
            + (item.endDate ? DateService.toLocalDMYFormat(item.endDate) : 'Indefinitely')
          )).join('\n'),
          actionName: 'modifyTitleEffectivityDates'
        },
        action: {
          id: item.titleId,
          icon: eyeIcon,
          toolTip: 'View title details',
          actionName: 'viewTitleDetailsFromTableTwo',
        },
        effectivityDates: item.effectivityDates.length > 0
          ? item.effectivityDates.map((date, i) => {
            const localStartDate = DateService.toLocalDMYFormat(date.startDate);
            const localEndDate = DateService.toLocalDMYFormat(date.endDate);
            const startDay = localStartDate?.split('/')[0];
            const startMonth = localStartDate?.split('/')[1];
            const startYear = localStartDate?.split('/')[2];
            const endDay = localEndDate?.split('/')[0];
            const endMonth = localEndDate?.split('/')[1];
            const endYear = localEndDate?.split('/')[2];
            const startDateWithoutTime = new Date(parseInt(startYear), parseInt(startMonth - 1), parseInt(startDay));
            const endDateWithoutTime = new Date(parseInt(endYear), parseInt(endMonth - 1), parseInt(endDay));
            const dateTodayWithoutTime = new Date();
            startDateWithoutTime.setHours(0, 0, 0, 0);
            dateTodayWithoutTime.setHours(0, 0, 0, 0);
            endDateWithoutTime.setHours(0, 0, 0, 0);
            return {
              id: i,
              startDate: date.startDate ? DateService.toFilterByLastSeen(date.startDate) : '',
              endDate: date.endDate ? DateService.toFilterByLastSeen(date.endDate) : '',
              historicalStartDate: !date.startDate || startDateWithoutTime < dateTodayWithoutTime,
              historicalEndDate: date.startDate && endDateWithoutTime < dateTodayWithoutTime,
            }
          })
          : [{ startDate: '', endDate: '' }],
        isHighlighted: item.overrode,
      })
    });
    setTableTwoData(excludedTitles);
    return excludedTitles;
  };

  const constructVerbiage = () => {
    const verbiageOne = {
      show: !selectedChannel.channelName,
      message: !selectedChannel.channelName ? verbiage[0].message : 'No programme titles found',
    };
    const verbiageTwo = {
      show: tableTwoData.length === 0,
      message: verbiage[1].message,
    };

    setVerbiage([verbiageOne, verbiageTwo]);
  };

  const handleGetProgrammeTitles = () => {
    setIsFetchingTitles(true);
    TitleExclusionService.getProgrammeTitles(
      constructQueryString(queryParameters)
    )
      .then((response) => {
        constructProgrammeTitles(response.data.data);

        setPaginationParameters({
          totalCount: response.data.totalData,
          pageSize: response.data.size,
          currentPage: response.data.page,
          totalPages: response.data.totalPages,
        });
      })
      .finally(() => setIsFetchingTitles(false));
  };

  const handleExportExcludedTitles = (proceed) => {
    if (selectedChannel?.channelName && !isExporting && tableTwoData.length > 0) {
      if (!isDirty || proceed) {
        setIsExporting(true);
        TitleExclusionService.exportExcludedTitles(selectedChannel.channelId)
        .then((response) => {
          const blob = new Blob([response.data], {
            type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
          });
          saveAs(
            blob,
            `${AuthService.getUserIdentity().organisationName}_op2mise_title_exclusion_export_${selectedChannel.channelName}_${DateService.toExportFormat(new Date())}.xlsx`
          );
          clearDirtyForm();
        })
        .finally(() => {
          setIsExporting(false);
          setShowConfirmExportModal(false);
          if (proceed) setIsFetchingExcludedTitles(false);
        });
      } else {
        setShowConfirmExportModal(true);
      }
    }
  };

  const handleGetExcludedTitles = (fromExport, fromDiscardAndImport, fromImport) => {
    setIsFetchingExcludedTitles(true);
    const queryString = selectedChannel.channelId ? `?channelID=${selectedChannel.channelId}` : '';
    TitleExclusionService.getExcludedTitles(queryString)
      .then((response) => {
        const excludedTitles = constructExcludedTitles(response.data);
        if (fromExport) handleExportExcludedTitles(true);
        if (fromDiscardAndImport) handleImportTitlesToExclude(storedSelectedFile, storedImportActionType, true, excludedTitles);
      })
      .finally(() => {
        setIsExcludedTitlesInitialLoaded(true);
        if (!fromImport && !fromDiscardAndImport) setIsFetchingExcludedTitles(false);
      });
  };

  const handleOnFilter = (selectedData) => {
    setQueryParameters({ ...queryParameters, ...selectedData });
  };

  const handleOnClearFilter = () => {
    setQueryParameters({
      pageNumber: 1,
      sortBy: queryParameters.sortBy,
      sortOrder: queryParameters.sortOrder,
      pageSize: queryParameters.pageSize,
      searchKey: queryParameters.searchKey,
    });
  };

  const handleOnSearch = (searchKey) => {
    setQueryParameters({ ...queryParameters, searchKey, pageNumber: 1 });
  };

  const handleOnSort = ({ sortBy, sortOrder }) => {
    setSortParameters({ order: sortOrder, sort: sortBy });
    setQueryParameters({ ...queryParameters, sortBy, sortOrder, pageNumber: 1 });
  };

  const handleOnPaginate = (pageNumber, pageSize) => {
    setQueryParameters({ ...queryParameters, pageNumber, pageSize });
  };

  const selectStartDate = (items) => {
    setSelectedProgrammeTitles(items);
  };

  const selectEndDate = (items) => {
    setSelectedProgrammeTitles(items);
  };

  const addDateRange = (items) => {
    setSelectedProgrammeTitles(items);
  };

  const deleteDateRange = (items) => {
    setSelectedProgrammeTitles(items);
  };
    
  const handleOnAddSelected = (toAddItems) => {
    setShouldClearSelected(false);
    const mappedItems = toAddItems.map((item, i) => ({
      titleId: item.titleId,
      titleName: item.titleName,
      action2:{
        id: item.titleId,
        icon: "",
        toolTip: "",
      },
      action: {
        id: item.titleId,
        icon: item.titleId === viewedItemId ? eyeSlashIcon : eyeIcon,
        toolTip: 'View title details',
        actionName: 'viewTitleDetailsFromTableTwo',
      },
      isHighlighted: item.isHighlighted,
      effectivityDates: [
        {
          id: i,
          startDate: null,
          endDate: null,
          dayStart: null,
          monthStart: null,
          yearStart: null,
          dayEnd: null,
          monthEnd: null,
          yearEnd: null,
        }
      ]
    }));

    setSelectedProgrammeTitles(mappedItems);
    setExcludeTitleFormType('Create');
  };

  const transferToTableTwo = (data) => {
    setShouldClearSelected(true);
    const itemIndex = tableTwoData.findIndex(o => o.titleId === data[0].titleId);
    if ((itemIndex > -1) && data.length === 1) {
      const newItems = JSON.parse(JSON.stringify(tableTwoData));
      newItems[itemIndex] = data[0];
      newItems[itemIndex].action2 = {
        id: newItems[itemIndex].titleId,
        icon: checkIfDatesAreDefinite(newItems[itemIndex].effectivityDates) ? DefiniteCalendarIcon : IndefiniteCalendarIcon,
        toolTip: newItems[itemIndex].effectivityDates.map((item) => (
          (item.startDate ? DateService.toFilterByLastSeen(item.startDate) : 'Immediately')
          + " - "
          + (item.endDate ? DateService.toFilterByLastSeen(item.endDate) : 'Indefinitely')
        )).join('\n'),
        actionName: 'modifyTitleEffectivityDates'
      }
      setTableTwoData(newItems);
    } else {
      const allItems = [ ...tableTwoData, ...data ];
      constructExcludedTitles(allItems)
    }
    setShowSetDateModal(false);
    setIsDirty(true);
  };

  const handleOnRemoveAll = () => {
    setTableTwoData([]);
    setIsDirty(true);
  };

  const handleOnRemoveSelected = (toRemoveItems) => {
    const mappedToRemoveItems = toRemoveItems.map((item) => item.titleId);
    const remainingItems = tableTwoData.filter(
      (item) => !mappedToRemoveItems.includes(item.titleId)
    );
    setTableTwoData(remainingItems);
    setIsDirty(true);
  };

  const handleSaveTitleExclusions = (fromDirtyForms, fromExport, fromImport) => {
    setIsSaving(true);    
    const parameters = {
      channelId: selectedChannel.channelId,
      titleExclusionDetails: tableTwoData.map((title) => ({
        titleId: title.titleId,
        effectivityDates: title.effectivityDates
          ? title.effectivityDates.map((date) => {
            const isStartDateString = typeof date.startDate === 'string';
            const isEndDateString = typeof date.endDate === 'string';
    
            if (isStartDateString || isEndDateString) {
              let [startDay, startMonth, startYear] = isStartDateString
              ? date.startDate.split('/')
              : [date.startDate, '', ''];

              if (!isStartDateString) {
                const currentDate = new Date(date.startDate);
                startDay = currentDate.getDate().toString();
                startMonth = (currentDate.getMonth() + 1).toString();
                startYear = currentDate.getFullYear().toString();
              }

              const [endDay, endMonth, endYear] = isEndDateString ? date.endDate.split('/') : date.endDate;
              return {
                startDate: new Date(Date.UTC(startYear, parseInt(startMonth) - 1, startDay)),
                endDate: new Date(Date.UTC(endYear, parseInt(endMonth) - 1, endDay)),
              };
            } else {
              return {
                startDate: date.startDate,
                endDate: date.endDate,
              };
            }
          })
          : [],
      })),
    };

    TitleExclusionService.saveTitleExclusions(parameters)
      .then(() => {
        setIsDirty(false);
        if (!fromDirtyForms) handleGetExcludedTitles(false, false, true);
        else {
          clearDirtyForm();
          confirmNavigation();
        }
        if (fromExport) handleExportExcludedTitles(true);
        if (fromImport) handleImportTitlesToExclude(storedSelectedFile, storedImportActionType, true)
        else success('Successfully updated title exclusions');
      })
      .finally(() => {
        setIsSaving(false);
        if (fromDirtyForms) hidePrompt();
      });
  };

  const handleOnChangeTableTwoData = (data) => {
    setShouldLoadTableOne(false);
    setTableTwoData(data);
  };

  const constructToDefaultItems = (items) => {
    setViewedItemId(null);
    setViewedItem(null);
    setTablesHeight(['371px', '371px']);

    let data = items;
    data = data.map((item) => ({
      ...item,
      action: { ...item.action, icon: eyeIcon },
    }));

    return data;
  };

  const handleGetProgrammeTitleDetails = (selectedId) => {
    TitleExclusionService.getProgrammeTitleDetails(selectedId).then(
      (response) => {
        setViewedItem(response.data);
      }
    );
  };

  const constructItemsAfterViewingDetails = (items, selectedId) => {
    setViewedItemId(selectedId);
    handleGetProgrammeTitleDetails(selectedId);
    setTablesHeight(['576px', '576px']);

    let data = items;
    data = data.map((item) =>
      item.titleId === selectedId
        ? {
            ...item,
            scrollTo: true,
            action: { ...item.action, icon: eyeSlashIcon },
          }
        : {
            ...item,
            scrollTo: false,
            action: { ...item.action, icon: eyeIcon },
          }
    );

    return data;
  };

  const viewTitleDetailsFromTableOne = (selectedId) => {
    setTableTwoData(constructToDefaultItems(tableTwoData));

    if (selectedId === viewedItemId) {
      setTableOneData(constructToDefaultItems(tableOneData));
    } else {
      setTableOneData(
        constructItemsAfterViewingDetails(tableOneData, selectedId)
      );
      setTimeout(() => {
        setShouldTableOneScrollToView(true);
      }, 100);
    }
  };

  const viewTitleDetailsFromTableTwo = (selectedId) => {
    setTableOneData(constructToDefaultItems(tableOneData));

    if (selectedId === viewedItemId) {
      setTableTwoData(constructToDefaultItems(tableTwoData));
    } else {
      setTableTwoData(
        constructItemsAfterViewingDetails(tableTwoData, selectedId)
      );
      setTimeout(() => {
        setShouldTableTwoScrollToView(true);
      }, 100);
    }
  };

  const modifyTitleEffectivityDates = (rowDetails) => {
    const title = JSON.parse(JSON.stringify(rowDetails));
    title.effectivityDates = title.effectivityDates
    ? title.effectivityDates.sort((a, b) => (
      new Date(a.startDate) - new Date(b.startDate)
    ))
    : [{
        id: 0,
        startDate: '',
        endDate: '',
      }];
      
    setSelectedProgrammeTitles([title]);
    setShowSetDateModal(true);
    setExcludeTitleFormType('Update');
  };

  const handleOnAction = (actionName, selectedId, rowDetails) => {
    switch (actionName) {
      case 'viewTitleDetailsFromTableOne':
        viewTitleDetailsFromTableOne(selectedId);
        break;
      case 'viewTitleDetailsFromTableTwo':
        viewTitleDetailsFromTableTwo(selectedId);
        break;
      case 'modifyTitleEffectivityDates':
        modifyTitleEffectivityDates(rowDetails);
        break;
      default:
    }
  };
  
  const handleShowModal = () => {
    setShowSetDateModal(true);
  }

  const handleHideModal = () => {
    setShowSetDateModal(false);
  }

  const onHideDetails = () => {
    setViewedItem(null);
    setViewedItemId(null);
    setTablesHeight(['331px', '331px']);

    const defaultTableOneData = tableOneData.map((item) => ({
      ...item,
      action: { ...item.action, icon: eyeIcon },
    }));
    const defaultTableTwoData = tableTwoData.map((item) => ({
      ...item,
      action: { ...item.action, icon: eyeIcon },
    }));

    setTableOneData(defaultTableOneData);
    setTableTwoData(defaultTableTwoData);
  };

  const handleGetChannels = () => {
    PrioritisationService.getChannels().then((response) => {
      setChannels(response.data);
    });
  };

  const handleOnSelectChannel = (channel, proceed) => {
    if (isDirty && !proceed) {
      setNextChannel(channel);
      setShowConfirmLeaveModal(true);
    } else {
      setSelectedChannel(channel);
      setQueryParameters({ ...queryParameters, channelId: channel.channelId });
      handleSaveTitleExclusionSession(channel.channelId);
    }
  };

  const handleGetTitleExclusionSession = () => {
    TitleExclusionService.getTitleExclusionSession()
    .then((response) => {
      setQueryParameters({ ...queryParameters, channelId: response.data.channelId })
      setSelectedChannel(response.data);
      setIsFetchingTitles(false);
      setIsFetchingExcludedTitles(false);
    });
  };

  const handleSaveTitleExclusionSession = (channelId) => {
    TitleExclusionService.saveTitleExclusionSession({ channelId });
  };

  const onHideConfirmLeaveModal = () => {
    setShowConfirmLeaveModal(false);
  };

  const clearDirtyForm = () => {
    setIsDirty(false);
    setNextChannel({});
  }

  const handleOnDiscardChanges = () => {
    setShowConfirmLeaveModal(false);
    handleOnSelectChannel(nextChannel, true);
    clearDirtyForm();
  };

  const handleOnSaveChanges = () => {
    handleSaveTitleExclusions(true);
    setSelectedChannel(nextChannel);
    setShowConfirmLeaveModal(false);
  };

  const openImportTitleToExcludeModal = () => {
    if (selectedChannel?.channelName) setShowTitleToExcludeModal(true);
  };

  const closeImportTitleToExcludeModal = () => {
    setShowTitleToExcludeModal(false);
  };

  const combineEffectivityDatesOfDuplicateTitles = (dataFromImport, dataFromTableTwo) => {
    const uniqueTitles = {};
    const importedData = dataFromImport.filter((title) => title.imported);
    const allData = [...importedData, ...(dataFromTableTwo ? dataFromTableTwo : [])];

    allData.forEach(title => {
      const { titleId } = title;
  
      if (!uniqueTitles[titleId]) {
        uniqueTitles[titleId] = {
          ...title,
          effectivityDates: [],
        };
      }
  
      if (title.imported) {
        uniqueTitles[titleId].effectivityDates.push({
          id: uniqueTitles[titleId].effectivityDates.length + 1,
          startDate: moment.utc(title.startDate, 'DD/MM/YYYY'),
          endDate: moment.utc(title.endDate, 'DD/MM/YYYY'),
        });
      } else {
        uniqueTitles[titleId].effectivityDates.push(...title.effectivityDates);
      }
    });
    
  
    const combinedTitles = Object.values(uniqueTitles);
    return combinedTitles;
  };

  const executeImportTitlesToExclude = (data, actionType, excludedTitles) => {
    setImportedTitlesToExclude(data);
    const itemsToExclude = actionType !== 2
      ? combineEffectivityDatesOfDuplicateTitles(data)
      : combineEffectivityDatesOfDuplicateTitles(data, (excludedTitles || tableTwoData));
    constructExcludedTitles(itemsToExclude);
  };

  const closeOverrideImportTitlesToExcludeModal = () => {
    setShowOverrideExcludedTitlesModal(false);
  };

  const handleImportTitlesToExclude = (file, actionType, proceed, excludedTitles) => {
    if (tableTwoData.length > 0 && !actionType) {
      setStoredSelectedFile(file);
      setShowTitleToExcludeModal(false);
      setShowOverrideExcludedTitlesModal(true);
    } else {
      if (isDirty && !proceed && actionType === 2) {
        setStoredSelectedFile(file);
        setStoredImportActionType(actionType);
        setShowConfirmImportModal(true);
        setShowImportTitlesToExclude(false);
        setShowOverrideExcludedTitlesModal(false);
      } else {
        setIsImporting(true);
        if (actionType === 1) setIsOverwriting(true);
        if (actionType === 2) setIsOverriding(true);
        const formData = new FormData();
        formData.append('file', file);

        TitleExclusionService.importTitlesToExclude(
          formData,
          file.name.split('.').pop(),
          selectedChannel.channelId,
          actionType === 2,
        ).then((response) => {
          executeImportTitlesToExclude(response.data, actionType, excludedTitles);
          setIsDirty(true);
          setShowImportTitlesToExclude(true);
          if (proceed) {
            success('Successfully updated and imported title exclusion');
            setIsFetchingExcludedTitles(true);
          }
        }).catch((error) => {
          danger(error.response.data.message);
        }).finally(() => {
          setIsImporting(false);
          setIsOverwriting(false);
          setIsOverriding(false);
          setShowTitleToExcludeModal(false);
          if (proceed) setIsFetchingExcludedTitles(false);
          setShowConfirmImportModal(false);
          setShowOverrideExcludedTitlesModal(false);
        });
      }
    }
  };

  const handleOverwriteExcludedTitles = () => {
    handleImportTitlesToExclude(storedSelectedFile, 1, false);
  };

  const handleOverrideExcludedTitles = () => {
    handleImportTitlesToExclude(storedSelectedFile, 2, false);
  };

  const handleDownloadSampleTemplate = () => {
    TitleExclusionService.exportSampleTitleExclusionExcelTemplate()
    .then((response) => {
      const blob = new Blob([response.data], {
        type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
      });
      saveAs(blob, 'title-exclusion-sample-template.xlsx');
    });
  };

  useEffect(() => {
    handleGetUser();
    handleGetChannels();
    handleGetTitleExclusionSession();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (isExcludedTitlesInitialLoaded && shouldLoadTableOne && titleSource && selectedChannel.channelId) {
      handleSetTableParameters(queryParameters);
      handleGetProgrammeTitles();
    }

    setShouldLoadTableOne(true);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [queryParameters, tableTwoData.length, isExcludedTitlesInitialLoaded, titleSource, selectedChannel]);

  useEffect(() => {
    if (selectedChannel.channelId) {
      handleGetExcludedTitles();
    }
  }, [selectedChannel]);

  useEffect(() => {
    constructVerbiage();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [tableTwoData, selectedChannel]);

  return (
    <>
      <BannerComponent
        componentName="Title Exclusions"
        isAddButtonHidden
        isSearchHidden
      />
      <hr style={{ borderTop: '1px  solid #b1b1b1', marginBottom: '8px' }} />

      <div className={styles.selectChannel}>
        <label className={styles.selectChannelLabel}>Channel: </label>
        <SelectComponent
          text={selectedChannel.channelName || 'Please select a channel'}
          list={channels}
          onSelect={handleOnSelectChannel}
          width="300px"
          withSearch
        />

        <img
          className={`${styles.uploadFileIcon} ${!selectedChannel?.channelName ? styles.disabledUploadFileIcon : ''}`}
          title="Import titles to exclude"
          src={uploadFileIcon}
          onClick={openImportTitleToExcludeModal}
        />

        <img
          className={`${styles.downloadFileIcon}
          ${tableTwoData.length === 0 ? styles.disabledDownloadFileIcon : ''} ${isExporting ? styles.isExportingDownloadFileIcon : ''}`}
          src={downloadFileIcon}
          title="Export the excluded programme titles"
          onClick={() => handleExportExcludedTitles(false)}
        />
      </div>

      <div style={{ marginTop: '15px', marginBottom: '7px' }}>
        <DualTable
          tableHeaders={[
            titleSource === 1
            ? tableOneHeader
            : tableOneImportHeader,
            tableTwoHeader
          ]}
          tableDatas={[tableOneData, tableTwoData]}
          tablesHeight={tablesHeight}
          tableLabels={tableLabels}
          noDataVerbiage={verbiage}
          isFetchingDatas={[isFetchingTitles, isFetchingExcludedTitles]}
          tableParameters={tableParameters}
          sortParameters={sortParameters}
          paginationParameters={paginationParameters}
          onFilter={handleOnFilter}
          onClearFilters={handleOnClearFilter}
          onSearch={handleOnSearch}
          onSort={handleOnSort}
          onPaginate={handleOnPaginate}
          onAddSelected={handleOnAddSelected}
          onRemoveAll={handleOnRemoveAll}
          onRemoveSelected={handleOnRemoveSelected}
          onChangeTableTwoData={handleOnChangeTableTwoData}
          onAction={handleOnAction}
          shouldScrollToView={[
            shouldTableOneScrollToView,
            shouldTableTwoScrollToView,
          ]}
          setShouldScrollToView={[
            setShouldTableOneScrollToView,
            setShouldTableTwoScrollToView,
          ]}
          onShowSetDateModal={handleShowModal}
          shouldClearSelected={shouldClearSelected}
        />
      </div>

      <ShowSetTitleExclusionDateModalComponent
        show={showSetDateModal}
        hideModal={handleHideModal}
        formType={excludeTitleFormType}
        selectedProgrammeTitleList={selectedProgrammeTitles}
        selectStartDate={selectStartDate}
        selectEndDate={selectEndDate}
        onAddDateRange={addDateRange}
        onDeleteRange={deleteDateRange}
        onAddTitles={transferToTableTwo}

      />

      {viewedItemId && viewedItem && (
        <>
          {titleSource === 1 ? (
            <ProgramDetailsComponent
              component="Title Group"
              program={viewedItem}
              callbackCloseDetails={onHideDetails}
            />
          ) : (
            <ImportProgramDetailsComponent
              component="Title Group"
              program={viewedItem}
              callbackCloseDetails={onHideDetails}
            />
          )}
        </>
      )}

      <div style={{ textAlign: 'right', marginTop: '8px' }}>
        <Button
          isLoading={isSaving}
          text="Save"
          disabled={!isDirty}
          onClick={handleSaveTitleExclusions}
        />
      </div>

      <ConfirmExportModal
        show={showConfirmExportModal}
        onHide={() => setShowConfirmExportModal(false)}
        onDiscard={() => handleGetExcludedTitles(true)}
        onExport={() => handleSaveTitleExclusions(false, true)}
        isExporting={isExporting}
      />

      <ConfirmImportModal
        show={showConfirmImportModal}
        onHide={() => setShowConfirmImportModal(false)}
        onDiscard={() => handleGetExcludedTitles(false, true)}
        onImport={() => handleSaveTitleExclusions(false, false, true)}
        isImporting={isSaving || isImporting}
      />

      <ImportModal
        title="Import titles to exclude"
        show={showTitleToExcludeModal}
        onImport={handleImportTitlesToExclude}
        onHide={closeImportTitleToExcludeModal}
        onClickSampleTemplate={handleDownloadSampleTemplate}
        isImporting={isImporting}
      />

      <ImportTitleToExcludeResultModal
        data={importedTitlesToExclude}
        show={showImportTitlesToExcludeModal}
        onHide={() => setShowImportTitlesToExclude(false)}
      />

      <OverrideExcludedTitlesModal
        show={showOverrideExcludedTitlesModal}
        onHide={closeOverrideImportTitlesToExcludeModal}
        onOverwrite={handleOverwriteExcludedTitles}
        onOverride={handleOverrideExcludedTitles}
        isOverwriting={isOverwriting}
        isOverriding={isOverriding}
      />

      {/* When changing selected channel inside the page */}
      <ConfirmLeavePageModalComponent
        show={showConfirmLeaveModal}
        onDiscard={handleOnDiscardChanges}
        onSave={handleOnSaveChanges}
        onHide={onHideConfirmLeaveModal}
        isSaving={isSaving}
      />

      {/* When leaving outside the page */}
      <ConfirmLeavePageModalComponent
        show={showPrompt}
        onDiscard={confirmNavigation}
        onSave={() => handleSaveTitleExclusions(true)}
        onHide={cancelNavigation}
        isSaving={isSaving}
      />
    </>
  );
}

export default TitleExclusionComponent;
