import React, { useState, useRef, useEffect } from 'react';
import { Modal } from 'react-bootstrap';
import PropTypes from 'prop-types';
import styles from './styles.module.css';
import Button from '../button/Button';

function ImportModal({
    title,
    show,
    onHide,
    onImport,
    onClickSampleTemplate,
    isImporting,
}) {
  const chooseFileRef = useRef();
  const acceptableFiles = ['xlsx', 'xls', 'csv'];
  const [selectedFile, setSelectedFile] = useState();
  const [errorMessage, setErrorMessage] = useState('');

  const handleUploadButton = () => {
    chooseFileRef.current.click();
  };

  const onFileChangeCapture = (e) => {
    const file = e.target.files[0];
    setSelectedFile(file);
  };

  const onClickImport = () => {
    const fileExtension = selectedFile.name.split('.').pop();

    if (acceptableFiles.includes(fileExtension)) {
      onImport(selectedFile);
    } else {
      setErrorMessage('Uploaded file type is not supported. Please use files in the following formats (xlsx, csv)');
    }
  };

  const onClickCancel = () => {
    onHide();
    setSelectedFile();
    setErrorMessage('');
  };

  useEffect(() => {
    if (!show) {
      setSelectedFile();
      setErrorMessage('');
    }
  }, [show]);

  return (
    <Modal show={show} centered size="lg" onHide={onHide}>
      <div className={styles.body}>
        <div>
            <div style={{ fontWeight: 'bolder' }}>{title || 'Import'}</div>
                <p className={styles.smallText}>
                    Download a
                    {' '}
                    <span
                        className={styles.sampleTemplateText}
                        onClick={onClickSampleTemplate}
                        onKeyDown={onClickSampleTemplate}
                        role="presentation"
                    >
                        sample template
                    </span>
                    {' '}
                    to see an example of the required format.
                </p>
                <p className={styles.errorMessage}>
                    {errorMessage}
                </p>

            <div className="d-flex" >
                <Button
                    text="Choose file"
                    size="lg"
                    style={{ marginTop: '30px', marginLeft: '0' }}
                    onClick={handleUploadButton}
                />
                <input
                    ref={chooseFileRef}
                    hidden
                    type="file"
                    accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                    onChange={onFileChangeCapture}
                />

                <p className={styles.fileName} title={selectedFile && selectedFile.name}>
                    {selectedFile && selectedFile.name}
                </p>
            </div>

            <div className='pt-2'>
                <small>Acceptable format: Excel, CSV</small>
            </div>
        </div>

        <div className={styles.buttons}>
          <Button text="Cancel" defaultBtn onClick={onClickCancel} />
          <Button text="Import" onClick={onClickImport} disabled={!selectedFile} isLoading={isImporting} />
        </div>
      </div>
    </Modal>
  );
}
ImportModal.propTypes = {
  onImport: PropTypes.func.isRequired,
  onHide: PropTypes.func.isRequired,
  onClickSampleTemplate: PropTypes.func.isRequired,
  show: PropTypes.bool,
};
ImportModal.defaultProps = {
  show: false,
};
ImportModal.displayName = 'ImportModal';

export default ImportModal;
