import React, { useState } from 'react';
import { Modal } from 'react-bootstrap';
import styles from '../../../../shared/components/confirm-leave-modal/styles.module.css';
import Button from '../../../../shared/components/button/Button';

const ConfirmImportModal = ({ show, onImport, onDiscard, onHide, isImporting }) => {
  const [clickedButton, setClickedButton] = useState('');

  const handleOnImport = () => {
    onImport();
    setClickedButton('Import');
  };

  const handleOnDiscard = () => {
    onDiscard();
    setClickedButton('Discard');
  };

  return (
    <Modal
      size="md"
      show={show}
      onHide={onHide}
      aria-labelledby="confirm-import-modal"
    >
      <Modal.Body className={styles.promptModalBody}>
        <div>
          You have unsaved changes to the excluded programme titles. Import the current
          version will not include these changes. Do you want to save your
          changes before importing or proceed with the current version?
        </div>
        <div>
          <div className={styles.button}>
            <Button
              text="Save and import"
              onClick={handleOnImport}
              style={{ width: '100%' }}
              isLoading={isImporting && clickedButton === 'Import'}
            />
          </div>
          <div className={styles.button}>
            <Button
              text="Discard changes and import"
              onClick={handleOnDiscard}
              style={{ width: '100%' }}
              isLoading={isImporting && clickedButton === 'Discard'}
            />
          </div>
          <div className={styles.button}>
            <Button
              text="Cancel"
              defaultBtn
              onClick={onHide}
              style={{ width: '100%' }}
            />
          </div>
        </div>
      </Modal.Body>
    </Modal>
  );
};

export default ConfirmImportModal;
