import React, { useState, useEffect, useRef } from 'react';
import PropTypes from 'prop-types';

import styles from './styles.module.css';
import {
  angleDownEndLineIcon,
  angleDownIcon,
  angleUpEndLineIcon,
  angleUpIcon,
  arrowLeftIcon,
  arrowRightIcon,
} from '../../assets/index';

import Table from '../table/Table';

function DualTable({
  withOrdering,
  tableHeaders = [],
  tableDatas = [],
  tablesHeight = [],
  tableLabels = [],
  noDataVerbiage = [],
  isFetchingDatas = [],
  tableParameters,
  sortParameters,
  paginationParameters,
  onFilter,
  onClearFilters,
  onSearch,
  onSort,
  onPaginate,
  onAddSelected,
  onRemoveAll,
  onRemoveSelected,
  onChangeTableTwoData,
  onAction,
  shouldScrollToView,
  setShouldScrollToView,
  onShowSetDateModal,
  shouldClearSelected,
}) {
  const [tableOneClearSelected, setTableOneClearSelected] = useState(false);
  const [tableTwoClearSelected, setTableTwoClearSelected] = useState(false);
  const [tableOneItems, setTableOneItems] = useState([]);
  const [allTableTwoItems, setAllTableTwoItems] = useState([]);
  const [tableTwoItems, setTableTwoItems] = useState([]);
  const [toAddItems, setToAddItems] = useState([]);
  const [toRemoveItems, setToRemoveItems] = useState([]);
  const [searchKey, setSearchKey] = useState('');
  const [searchKeyHistory, setSearchKeyHistory] = useState('');
  const [hasSearched, setHasSearched] = useState(false);
  const searchRef = useRef();
  const [shouldScroll, setShouldScroll] = useState(false);
  const [isInitialRender, setIsInitialRender] = useState(true);
  const [tableTwoSortParameters, setTableTwoSortParameters] = useState({
    order: 1,
    sort: 1,
  });
  const [
    tableTwoPaginationParameters,
    setTableTwoPaginationParameters,
  ] = useState({ pageSize: 50, currentPage: 1 });
  
  const [excludedParamters, setExcludedParamters] = useState({
    page: 1,
    size: 50,
  });

  const handleTableOneMultiSelect = (items) => {
    setToAddItems(items);
  };

  const handleTableTwoMultiSelect = (items) => {
    setToRemoveItems(items);
  };

  const handleOnAddSelected = () => {
    onAddSelected(toAddItems, !withOrdering && tableTwoSortParameters.order);
    if (shouldClearSelected === undefined) {
      setTableOneClearSelected(true);
      setToAddItems([]);
    }
  };

  const handleShowModal = () => {
    handleOnAddSelected();
    onShowSetDateModal();
  }

  const handleRemoveAll = () => {
    onRemoveAll();
    setToRemoveItems([]);
    setTableTwoClearSelected(true);
  };

  const handleRemoveSelected = () => {
    onRemoveSelected(toRemoveItems);
    setToRemoveItems([]);
    setTableTwoClearSelected(true);
  };

  const onChangeSetSearchKey = (e) => {
    setSearchKey(e.target.value);
  };

  const handleOnSearch = () => {
    onSearch(searchKey);
    setSearchKeyHistory(searchKey);
    setHasSearched(true);
  };

  const onKeyDownTriggerSearch = (e) => {
    if (e.key === 'Enter') handleOnSearch();
  };

  const handleOnClearSearch = () => {
    onSearch('');
    setSearchKey('');
    setSearchKeyHistory('');
    setHasSearched(false);
  };

  const renderNoDataComponent = (message, tableHeight) => {
    const component = (
      <div
        className={styles.noDataComponent}
        style={{ height: `calc(100vh + 120px - ${tableHeight})` }}
      >
        <div className={styles.textWrapper}>
          <p className={styles.verbiage}>{message}</p>
        </div>
      </div>
    );

    return component;
  };

  // Table two data sorting, pagination and ordering
  const sortTableTwoData = (sortOrder) => {
    let tableTwoData = JSON.parse(JSON.stringify(allTableTwoItems));
    if (sortOrder === 1) {
      tableTwoData = tableTwoData.sort((a, b) => {
        if (a.titleName.toLowerCase() > b.titleName.toLowerCase()) return 1;
        return -1;
      });
    } else {
      tableTwoData = tableTwoData.sort((a, b) => {
        if (a.titleName.toLowerCase() > b.titleName.toLowerCase()) return -1;
        return 1;
      });
    }

    setAllTableTwoItems(tableTwoData);
  };

  const handleOnSortTableTwo = ({ sortOrder, sortBy }) => {
    setTableTwoSortParameters({ order: sortOrder, sort: sortBy });
    sortTableTwoData(sortOrder);
  };

  const getFromIndex = () => {
    const item = toRemoveItems[0];
    const fromIndex = allTableTwoItems.findIndex((o) => (
      o[Object.keys(o)[0]] === item[Object.keys(item)[0]]
    ));

    return fromIndex;
  };

  const moveItemFromArray = (toIndex) => {
    const fromIndex = getFromIndex();
    const item = allTableTwoItems[fromIndex];
    const newAllTableTwoItems = [...allTableTwoItems];
    newAllTableTwoItems.splice(fromIndex, 1);
    newAllTableTwoItems.splice(toIndex, 0, item);

    return newAllTableTwoItems;
  };

  const findPageAfterDragging = (direction) => {
    let itemIndex = 0;
    switch (direction) {
      case 'first':
        itemIndex = 0;
        break;
      case 'up':
        itemIndex = getFromIndex() - 1;
        break;
      case 'down':
        itemIndex = getFromIndex() + 1;
        break;
      case 'last':
        itemIndex = allTableTwoItems.length - 1;
        break;
      default:
        break;
    }
    const pageSize = !tableTwoPaginationParameters.size
      ? 50
      : tableTwoPaginationParameters.size;
    // eslint-disable-next-line
    return Math.ceil(++itemIndex / pageSize);
  };

  const orderTableTwoData = (list, direction) => {
    setAllTableTwoItems(list);
    const pageNumberAfterDragging = findPageAfterDragging(direction);
    const pageNumber = pageNumberAfterDragging;
    const { pageSize } = tableTwoPaginationParameters;
    const toSkip = pageNumber === 1 ? 0 : (pageNumber - 1) * pageSize;
    const toTake = list.slice(toSkip, toSkip + pageSize);
    setTableTwoItems(toTake);
    setTableTwoPaginationParameters({
      pageSize,
      totalCount: toTake.length,
      currentPage: pageNumber,
      totalPages: Math.ceil(list.length / pageSize),
    });
  };

  const setScrollTo = () => {
    setTimeout(() => {
      setShouldScrollToView(true);
    }, 100);
  };

  const dragToTop = () => {
    const newAllTableTwoItems = moveItemFromArray(0);
    orderTableTwoData(newAllTableTwoItems, 'first');
    setScrollTo();
  };

  const dragUp = () => {
    if (getFromIndex() - 1 >= 0) {
      const newAllTableTwoItems = moveItemFromArray(getFromIndex() - 1);
      orderTableTwoData(newAllTableTwoItems, 'up');
      setScrollTo();
    }
  };

  const dragDown = () => {
    const newAllTableTwoItems = moveItemFromArray(getFromIndex() + 1);
    orderTableTwoData(newAllTableTwoItems, 'down');
    setScrollTo();
  };

  const dragToBottom = () => {
    const newAllTableTwoItems = moveItemFromArray(allTableTwoItems.length);
    orderTableTwoData(newAllTableTwoItems, 'last');
    setScrollTo();
  };

  const onPaginateTableTwoData = (pageNumber, size) => {
    setToRemoveItems([]);
    setTableTwoClearSelected(true);
    let page = pageNumber;
    let toSkip = page === 1 ? 0 : (page - 1) * size;
    let allTableTwoList = JSON.parse(JSON.stringify(allTableTwoItems));

    if (tableTwoSortParameters.order === 1) {
      allTableTwoList = allTableTwoList.sort((a, b) => {
        if (a.titleName.toLowerCase() > b.titleName.toLowerCase()) return 1;
        return -1;
      });
    } else {
      allTableTwoList = allTableTwoList.sort((a, b) => {
        if (a.titleName.toLowerCase() > b.titleName.toLowerCase()) return -1;
        return 1;
      });
    }

    while (toSkip >= allTableTwoList.length) {
      page -= 1;
      toSkip = page === 1 ? 0 : (page - 1) * size;
    }

    const tableTwoPaginatedList = allTableTwoList.slice(toSkip, toSkip + size);
    setTableTwoItems(tableTwoPaginatedList);
    setTableTwoPaginationParameters({
      totalCount: allTableTwoList.length,
      pageSize: size,
      currentPage: pageNumber === 0 ? 1 : pageNumber,
      totalPages: Math.ceil(allTableTwoList.length / size),
    });
  };

  const orderButtons = [
    { id: 1, icon: angleUpEndLineIcon, action: dragToTop },
    { id: 2, icon: angleUpIcon, action: dragUp },
    { id: 3, icon: angleDownIcon, action: dragDown },
    { id: 4, icon: angleDownEndLineIcon, action: dragToBottom },
  ];

  const addHasOrderingToItem = () => {
    const selectedItem = toRemoveItems[0];
    const allItems = allTableTwoItems;
    const selectedItemIndex = allTableTwoItems.findIndex((o) => (
      o[Object.keys(o)[0]] === selectedItem[Object.keys(selectedItem)[0]]
    ));
    allItems[selectedItemIndex].scrollTo = true;

    setAllTableTwoItems(allItems);
  };

  useEffect(() => {
    if (
      tableTwoPaginationParameters.currentPage >
      tableTwoPaginationParameters.totalPages
    ) {
      setTableTwoPaginationParameters((prevState) => ({
        ...prevState,
        currentPage: tableTwoPaginationParameters.totalPages,
      }));
      setTableTwoPaginationParameters((prevState) => ({
        ...prevState,
        page: tableTwoPaginationParameters.totalPages,
      }));

      const { size } = excludedParamters;
      const toSkip =
      tableTwoPaginationParameters.totalPages === 1
          ? 0
          : (tableTwoPaginationParameters.totalPages - 1) * size;
      const selectedItems = tableTwoItems.slice(toSkip, toSkip + size);
      setTableTwoItems(selectedItems);
    }
  }, [tableTwoPaginationParameters])

  useEffect(() => {
    if (toRemoveItems.length === 1) addHasOrderingToItem();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [toRemoveItems]);

  useEffect(() => {
    if (tableDatas[0]) setTableOneItems(tableDatas[0]);
    if (tableDatas[1]) setAllTableTwoItems(tableDatas[1]);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [tableDatas]);

  useEffect(() => {
    if (allTableTwoItems && allTableTwoItems.length > 0) {
      onPaginateTableTwoData(
        tableTwoPaginationParameters.currentPage,
        tableTwoPaginationParameters.pageSize,
      );
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [allTableTwoItems]);

  useEffect(() => {
    const sortedAllTableTwoItems = allTableTwoItems.sort((a, b) => {
      const aValue = a[Object.keys(a)[0]];
      const bValue = b[Object.keys(b)[0]];
      return aValue - bValue;
    });

    const sortedTableDataTwo = tableDatas[1].sort((a, b) => {
      const aValue = a[Object.keys(a)[0]];
      const bValue = b[Object.keys(b)[0]];
      return aValue - bValue;
    });

    const isEqual = JSON.stringify(sortedAllTableTwoItems) === JSON.stringify(sortedTableDataTwo);
    if (!isEqual) onChangeTableTwoData(allTableTwoItems);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [allTableTwoItems]);

  useEffect(() => {
    if (shouldClearSelected) setTableOneClearSelected(true);
  }, [shouldClearSelected]);

  useEffect(() => {
    const backdropClicked = (e) => {
      if (searchRef.current && !searchRef.current.contains(e.target)) {
        setSearchKey(searchKeyHistory);
      }
    };

    document.addEventListener('mousedown', backdropClicked);
    return () => document.removeEventListener('mousedown', backdropClicked);
  });

  return (
    <div className={styles.dualTable}>
      <div className={styles.tableOne}>
        <div className={styles.tableLabel}>{tableLabels[0]}</div>

        {!noDataVerbiage[0].show ? (
          <Table
            headers={tableHeaders[0]}
            list={tableOneItems}
            noDataVerbiage={noDataVerbiage[0].message}
            isFetchingData={isFetchingDatas[0]}
            queryParameters={tableParameters}
            sortParameters={sortParameters}
            paginationParameters={paginationParameters}
            onFilter={onFilter}
            onClearFilters={onClearFilters}
            onSearch={onSearch}
            onSort={onSort}
            onPaginate={onPaginate}
            onAction={onAction}
            shouldScrollToView={shouldScrollToView[0]}
            setShouldScrollToView={setShouldScrollToView[0]}
            clearSelected={tableOneClearSelected}
            setClearSelected={setTableOneClearSelected}
            onMultiSelect={handleTableOneMultiSelect}
            tableHeight={tablesHeight[0]}
          />
        ) : (
          renderNoDataComponent(noDataVerbiage[0].message, tablesHeight[0])
        )}
      </div>

      <div className={styles.actionButtons}>
        <button
          //onClick={handleOnAddSelected}
          onClick={handleShowModal}
          disabled={toAddItems.length === 0}
          type="button"
        >
          Add Selected
          <img style={{ marginTop: '11px' }} src={arrowRightIcon} alt="" />
        </button>
        <button
          onClick={handleRemoveAll}
          style={{ paddingTop: '9px' }}
          disabled={allTableTwoItems.length === 0}
          type="button"
        >
          <img style={{ marginTop: '2px' }} src={arrowLeftIcon} alt="" />
          <span style={{ marginLeft: '5px' }}>Remove All</span>
        </button>
        <button
          onClick={handleRemoveSelected}
          disabled={toRemoveItems.length === 0}
          type="button"
        >
          <img style={{ marginTop: '11px' }} src={arrowLeftIcon} alt="" />
          Remove Selected
        </button>
      </div>

      <div className={styles.tableTwo}>
        <div className={styles.tableLabel}>{tableLabels[1]}</div>

        <div style={{ display: 'flex', width: '100%' }}>
          {!noDataVerbiage[1].show ? (
            <Table
              headers={tableHeaders[1]}
              list={tableTwoItems}
              noDataVerbiage={noDataVerbiage[0].message}
              isFetchingData={isFetchingDatas[1]}
              sortParameters={!withOrdering && tableTwoSortParameters}
              paginationParameters={tableTwoPaginationParameters}
              onAction={onAction}
              clearSelected={tableTwoClearSelected}
              shouldScrollToView={shouldScroll || shouldScrollToView[1]}
              setShouldScrollToView={() => {
                setShouldScrollToView[1]();
                setShouldScroll();
              }}
              onMultiSelect={handleTableTwoMultiSelect}
              setClearSelected={setTableTwoClearSelected}
              onSort={!withOrdering && handleOnSortTableTwo}
              onPaginate={onPaginateTableTwoData}
              tableHeight={tablesHeight[1]}
            />
          ) : (
            renderNoDataComponent(noDataVerbiage[1].message, tablesHeight[1])
          )}

          {withOrdering && (
            <div className={styles.orderingButtons}>
              {orderButtons.map((button) => (
                <button
                  key={button.id}
                  className={styles.dragButton}
                  onClick={button.action}
                  disabled={
                    allTableTwoItems.length === 0
                    || toRemoveItems.length === 0
                    || toRemoveItems.length > 1
                  }
                >
                  <img src={button.icon} alt="" />
                </button>
              ))}
            </div>
          )}
        </div>
      </div>


    </div>
  );
}
DualTable.propTypes = {
  withOrdering: PropTypes.bool,
  tableHeaders: PropTypes.arrayOf(PropTypes.string),
  tableDatas: PropTypes.arrayOf(PropTypes.shape),
  tableLabels: PropTypes.arrayOf(PropTypes.string),
  noDataVerbiage: PropTypes.arrayOf(PropTypes.string),
  tablesHeight: PropTypes.arrayOf(PropTypes.string),
  isFetchingDatas: PropTypes.bool,
  tableParameters: PropTypes.shape,
  sortParameters: PropTypes.shape,
  paginationParameters: PropTypes.shape,
  onFilter: PropTypes.func,
  onClearFilters: PropTypes.func,
  onSearch: PropTypes.func,
  onSort: PropTypes.func,
  onPaginate: PropTypes.func,
  onAddSelected: PropTypes.func,
  onRemoveAll: PropTypes.func,
  onRemoveSelected: PropTypes.func,
  onChangeTableTwoData: PropTypes.func,
  onAction: PropTypes.func,
  shouldScrollToView: PropTypes.bool,
  setShouldScrollToView: PropTypes.func,
};
DualTable.defaultProps = {
  withOrdering: false,
  tableHeaders: [],
  tableDatas: [],
  noDataVerbiage: ['', ''],
  tableLabels: ['', ''],
  tablesHeight: [],
  isFetchingDatas: [true, true],
  tableParameters: null,
  sortParameters: null,
  paginationParameters: null,
  onFilter: null,
  onClearFilters: null,
  onSearch: null,
  onSort: null,
  onPaginate: null,
  onAddSelected: null,
  onRemoveAll: null,
  onRemoveSelected: null,
  onChangeTableTwoData: undefined,
  onAction: undefined,
  shouldScrollToView: false,
  setShouldScrollToView: null,
};
export default DualTable;
