import axios from 'axios';

const hostUrl = process.env.REACT_APP_HOST_URL;

const TitleExclusionService = {
  getProgrammeTitles(parameters) {
    const url = `${hostUrl}titleExclusion/getTitleExclusionProgrammeTitles${parameters}`;
    return axios.get(url);
  },

  getProgrammeTitleDetails(titleId) {
    const url = `${hostUrl}titleExclusion/getTitleExclusionTitleDetails?titleId=${titleId}`;
    return axios.get(url);
  },

  getExcludedTitles(parameters) {
    const url = `${hostUrl}titleExclusion/getTitleExclusions` + parameters;
    return axios.get(url);
  },

  getEffectivityDates(titleId) {
    const url = `${hostUrl}titleExclusion/getTitleEffectivityDatesCommand/${titleId}`;
    return axios.get(url)
  },

  saveTitleExclusions(titleIds) {
    const url = `${hostUrl}titleExclusion/saveTitleExclusions`;
    return axios.post(url, titleIds);
  },

  getTitleExclusionSession() {
    const url = `${hostUrl}titleExclusion/getTitleExclusionSession`;
    return axios.get(url)
  },

  saveTitleExclusionSession(json) {
    const url = `${hostUrl}titleExclusion/saveTitleExclusionSession`;
    return axios.post(url, json);
  },

  exportSampleTitleExclusionExcelTemplate() {
    const url = `${hostUrl}titleExclusion/exportSampleTitleExclusionExcelTemplate`;
    return axios.get(url, { responseType: 'arraybuffer' });
  },

  importTitlesToExclude(formData, fileExtension, channelId, isOverride) {
    const url = `${hostUrl}titleExclusion/importTitlesToExclude?fileExtension=${fileExtension}&channelId=${channelId}&isOverride=${isOverride}`;
    return axios.post(url, formData);
  },

  exportExcludedTitles(channelId) {
    const url = `${hostUrl}titleExclusion/exportExcludedTitles/${channelId}`;
    return axios.get(url, { responseType: 'arraybuffer' });
  },
};

export default TitleExclusionService;
