import React, { useState } from 'react';
import { Modal } from 'react-bootstrap';
import styles from '../../../../shared/components/confirm-leave-modal/styles.module.css';
import Button from '../../../../shared/components/button/Button';

const ConfirmExportModal = ({ show, onExport, onDiscard, onHide, isExporting }) => {
  const [clickedButton, setClickedButton] = useState('');

  const handleOnExport = () => {
    onExport();
    setClickedButton('Export');
  };

  const handleOnDiscard = () => {
    onDiscard();
    setClickedButton('Discard');
  };

  return (
    <Modal
      size="md"
      show={show}
      onHide={onHide}
      aria-labelledby="confirm-export-modal"
    >
      <Modal.Body className={styles.promptModalBody}>
        <div>
          You have unsaved changes to the title exclusion. Exporting the current
          version will not include these changes. Do you want to save your
          changes before exporting or proceed with the current version?
        </div>
        <div>
          <div className={styles.button}>
            <Button
              text="Save and export"
              onClick={handleOnExport}
              style={{ width: '100%' }}
              isLoading={isExporting && clickedButton === 'Export'}
            />
          </div>
          <div className={styles.button}>
            <Button
              text="Discard changes and export"
              onClick={handleOnDiscard}
              style={{ width: '100%' }}
              isLoading={isExporting && clickedButton === 'Discard'}
            />
          </div>
          <div className={styles.button}>
            <Button
              text="Cancel"
              defaultBtn
              onClick={onHide}
              style={{ width: '100%' }}
            />
          </div>
        </div>
      </Modal.Body>
    </Modal>
  );
};

export default ConfirmExportModal;
