import { Modal } from 'react-bootstrap';
import styles from './OverrideExcludedTitlesModal.module.css';
import Button from '../../../../shared/components/button/Button';

const OverrideExcludedTitlesModal = ({
    show,
    onHide,
    onOverwrite,
    onOverride,
    isOverwriting,
    isOverriding,
}) => {
    return (
        <Modal
            contentClassName={styles.modal}
            show={show}
            onHide={onHide}
            size="lg"
            centered>
            <div className={styles.content}>
                <p className={styles.description}>The title exclusion you are importing in this channel has an existing excluded titles.</p>

                <div className={styles.confirmationDescriptionWrapper}>
                    <b className={styles.description}>Do you want to overwrite the existing titles or override them</b>
                </div>

                <div className={styles.actionButtons}>
                    <Button text="Cancel" defaultBtn onClick={onHide} />
                    <Button
                        text="Overwrite"
                        onClick={onOverwrite}
                        isLoading={isOverwriting}
                        disabled={isOverriding}
                    />
                    <div className={styles.overrideButton}>
                        <Button
                            text="Override"
                            onClick={onOverride}
                            isLoading={isOverriding}
                            disabled={isOverwriting}
                        />
                    </div>
                </div>
            </div>
        </Modal>
    )
};

export default OverrideExcludedTitlesModal;