import styles from './ImportTitleToExcludeResultModal.module.css';
import { checkAltIcon, timesRedIcon } from '../../../../assets';
import Button from '../../../../shared/components/button/Button';
import { Modal } from 'react-bootstrap';
import { useState } from 'react';

const ImportTitleToExcludeResultModal = ({ show, onHide, data }) => {
    const [filter, setFilter] = useState(1);
    const radioButtons = [
        { id: 1, label: 'Show all' },
        { id: 2, label: 'Imported' },
        { id: 3, label: 'Not Imported' },
    ];

    const onClickSetFilter = (id) => {
        setFilter(id);
    };
    
    return (
        <Modal show={show} contentClassName={styles.modal} onHide={onHide} centered>
            <div className={styles.dialogContent}>
                <p className={styles.description}>
                    <b>{data?.filter(o => o.imported).length || 0}</b>
                    {" "}
                    titles out of
                    {" "}
                    <b>{data?.length || 0}</b>
                    {" "}
                    were successfully imported and validated!
                </p>

                <div className={styles.table}>
                    <div className={styles.tableHeader}>
                        <div>Programme Title</div>
                        <div>Duration</div>
                        <div>Start date</div>
                        <div>End date</div>
                        <div></div>
                        <div>Results</div>
                    </div>

                    <div className={styles.tableBody}>
                        {data.filter(o => {
                            if (filter === 1) return true;
                            if (filter === 2) return o.imported;
                            if (filter === 3) return !o.imported;
                        }).map((item) => (
                            <div className={styles.tableBodyRow}>
                                <div title={item.titleName}>{item.titleName}</div>
                                <div title={item.duration}>{item.duration}</div>
                                <div title={item.startDate}>{item.startDate}</div>
                                <div title={item.endDate}>{item.endDate}</div>
                                <div title={item.imported ? 'Imported' : 'Not Imported'}>
                                    <img src={item.imported ? checkAltIcon : timesRedIcon} alt="" />
                                </div>
                                <div title={item.importResult}>{item.importResult}</div>
                            </div>
                        ))}
                    </div>
                </div>

                <div className={styles.radioButtons}>
                    {radioButtons.map((button) => (
                        <div className={styles.formCheck}>
                            <input
                                className={`${styles.formCheckInput} form-check-input`}
                                type="radio"
                                name="successFilter"
                                onClick={() => onClickSetFilter(button.id)}
                                checked={filter === button.id}
                            />
                            <div className={styles.formCheckInputLabel}>
                                {button.label}
                            </div>
                        </div>
                    ))}
                </div>

                <div className={styles.closeButton}>
                    <Button text="Close" onClick={onHide} />
                </div>
            </div>
        </Modal>
    )
};

export default ImportTitleToExcludeResultModal;