import React, { useState, useEffect, useRef } from "react";
import { Dialog, DialogActionsBar } from "@progress/kendo-react-dialogs";
import stylesModal from "./Modal.module.css";
import Button from "../../../shared/components/button/Button";
import TitleExclusionDateFilterComponent from "../filters/title-exlucsion-date-filter/title-exlucsion-date.filter.component";
import AddButton from "../../../assets/icons/Vector.svg";
import TrashCan from "../../../assets/icons/Trash-Can.png";
import Checkbox from "../checkbox/Checkbox";
import CustomDateRangeInput from "../../../shared/components/custom-date-range/CustomDateRangeInput";

function ShowSetTitleExclusionDateModalComponent({
  show,
  hideModal,
  formType,
  selectedProgrammeTitleList,
  onAddTitles,
  selectStartDate,
  selectEndDate,
  onAddDateRange,
  onDeleteRange,
}) {
  const [visible, setVisible] = React.useState(false);
  const toggleDialog = () => {
    setVisible(!visible);
  };
  const min = new Date();
  const dateFilterRef = useRef();
  const [isDateComponent, setisDateComponent] = useState(false);
  const [titleIndex, setTitleIndex] = useState(0);
  const [subIndex, setSubIndex] = useState(0);
  const [isActive, setIsActive] = useState(false);
  const [hasNoStartDates, setHasNoStartDates] = useState([]);
  const [overlappingDates, setOverlappingDates]= useState([]);
  const [invalidDateRanges, setInvalidDateRanges] = useState([]);
  const [lessThanTodayDates, setLessThanTodayDates] = useState([]);
  const dateTodayWithoutTimeStamp = new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate());
  const [lastAddedId, setLastAddedId] = useState(0);

  const cancelButton = () => {
    setVisible(false);
    hideModal();
    setHasNoStartDates([]);
    setOverlappingDates([]);
    setInvalidDateRanges([]);
    setLessThanTodayDates([]);
    setSubIndex(0);
    setTitleIndex(0);
    setIsActive(false);
    setLastAddedId(0);
  };

  const showDateComponent = (index, index2) => {

    setisDateComponent(true);
  };

  const hideDateComponent = (e) => {
    if (!dateFilterRef.current?.contains(e.target)) {
      return false;
    }
    setisDateComponent(false);
    setTitleIndex(0);
    setSubIndex(0);
  };

  const dateRangeOverlaps = (a_start, a_end, b_start, b_end) => {
    const aStart = a_start && new Date(a_start);
    const aEnd = a_end && new Date(a_end);
    const bStart = b_start && new Date(b_start);
    const bEnd = b_end && new Date(b_end);

    if (aStart && bStart && !aEnd && !bEnd) return (aStart >= bStart) || (bStart >= aStart); // date range overlap but no end dates
    if (aStart && bStart && !aEnd) return (bStart <= aStart && bEnd >= aStart) || (aStart <= bStart && aStart <= bEnd); // date range overlap but no aEnd
    if (aStart && bStart && !bEnd) return (aStart <= bStart && aEnd >= bStart) || (bStart <= aStart && bStart <= aEnd); // date range overlap but no bEnd
    if (aStart && bStart) return (aStart >= bStart && aStart <= bEnd) || (bStart >= aStart && bStart <= aEnd); // date range overlap
  };

  const checkDateRangeIfValid = () => {
    let invalidDates = [];
    selectedProgrammeTitleList.map((title) => (
      title.effectivityDates.map((date) => {
        if (date.endDate && new Date(date.startDate) > new Date(date.endDate)) {
          invalidDates.push(`${title.titleId}-${date.id}`);
        }
      })
    ));
    setInvalidDateRanges(invalidDates);
    return invalidDates.length > 0;
  }

  const checkDateArrayOverlaps = () => {
    let overlappedDates = [];
    let datesLessThanToday = [];
    selectedProgrammeTitleList.map((title) => (
      title.effectivityDates.map((date, index) => {
        title.effectivityDates.map((otherDate, otherIndex) => {
          const isDateOverlapping = dateRangeOverlaps(
            date.startDate,
            date.endDate,
            otherDate.startDate,
            otherDate.endDate,
          ) && index !== otherIndex;
          const isDateLessThanToday = date.startDate && (new Date(date.startDate) < dateTodayWithoutTimeStamp);
          if (isDateOverlapping && (!date.historicalStartDate || !date.historicalEndDate)) overlappedDates.push(`${title.titleId}-${date.id}`);
          if (isDateLessThanToday && !date.historicalStartDate) datesLessThanToday.push(`${title.titleId}-${date.id}`);
        })
      })
    ));
    setOverlappingDates(overlappedDates);
    setLessThanTodayDates(datesLessThanToday);
    return overlappedDates.length > 0 || datesLessThanToday.length > 0;
  };

  const checkIfSomeHasNoStartDate = () => {
    let hasNoStartDate = [];
    selectedProgrammeTitleList.map((title) => {
      if (title.effectivityDates.every((date) => !date.startDate)) hasNoStartDate.push(title.titleId);
    });
    setHasNoStartDates(hasNoStartDate);
    return hasNoStartDate.length > 0;
  };

  const handleChange = (event, index, index2, disabledStart) => {
    setTitleIndex(index);
    setSubIndex(index2);
    if (!disabledStart) {
      selectDateStart(event.start, index , index2, event.dayStart, event.monthStart, event.yearStart);
      selectDateEnd(event.end, index , index2, event.dayEnd, event.monthEnd, event.yearEnd);
    } else {
      selectDateEnd(event.end, index , index2);
    }
  };

  const selectDateStart = (date, index , index2, dayStart, monthStart, yearStart) => {
    let items = [...selectedProgrammeTitleList];
    let item = { ...items[index] };
    let subItem = { ...item.effectivityDates[index2] }; 
    subItem.startDate = date;
    items[index].effectivityDates[index2].startDate = subItem.startDate;
    subItem.dayStart = dayStart;
    items[index].effectivityDates[index2].dayStart = subItem.dayStart;
    subItem.monthStart = monthStart;
    items[index].effectivityDates[index2].monthStart = subItem.monthStart;
    subItem.yearStart = yearStart;
    items[index].effectivityDates[index2].yearStart = subItem.yearStart;
    selectStartDate(items);
  };

  const selectDateEnd = (date, index , index2, dayEnd, monthEnd, yearEnd) => {
    let items = [...selectedProgrammeTitleList];
    let item = { ...items[index] };
    let subItem = { ...item.effectivityDates[index2] };
    subItem.endDate = date;
    items[index].effectivityDates[index2].endDate = subItem.endDate;
    subItem.dayEnd = dayEnd;
    items[index].effectivityDates[index2].dayEnd = subItem.dayEnd;
    subItem.monthEnd = monthEnd;
    items[index].effectivityDates[index2].monthEnd = subItem.monthEnd;
    subItem.yearEnd = yearEnd;
    items[index].effectivityDates[index2].yearEnd = subItem.yearEnd;
    selectEndDate(items);
  };

  const addDateRange = (index) => {
    let items = [...selectedProgrammeTitleList];
    let item = { ...items[index] };
    let subItem = item.effectivityDates;
    const max = Math.max(...subItem.map((subItemData) => subItemData.id));
    if (lastAddedId === 0) setLastAddedId(max + 1);
    else setLastAddedId(lastAddedId + 1);
    let effectivityDates = [
      {
        id: (lastAddedId === 0 ? max : lastAddedId) + 1,
        startDate: null,
        endDate: null,
      },
    ];

    var combinedArray = [...subItem, ...effectivityDates];
    items[index].effectivityDates = combinedArray;
    onAddDateRange(items);
  };

  const activateCheckbox = (checkState) => {
    setIsActive(checkState);
    
    let items = [...selectedProgrammeTitleList];
    let item = { ...items[0] };
    let subItem = item.effectivityDates;
    let startDate = subItem[0].startDate;
    let endDate = subItem[0].endDate;
    
    for (let i = 0; i < items.length; i++) {
      items[i].effectivityDates[0].startDate = checkState && startDate
        ? startDate
        : items[i].effectivityDates[0].startDate;
      items[i].effectivityDates[0].endDate = checkState && endDate
        ? endDate
        : items[i].effectivityDates[0].endDate;
    }
    onAddDateRange(items);
  };
  
  const deleteDateRange = (index, index2) => {
    let items = [...selectedProgrammeTitleList];
    let item = { ...items[index] };
    let subItem = item.effectivityDates;
    subItem.splice(index2, 1);
    items[index].effectivityDates = subItem;
    onDeleteRange(items);
  };

  const handleOnExcludeTitles = () => {
    const hasOverlaps = checkDateArrayOverlaps();
    const hasNoStartDates = checkIfSomeHasNoStartDate();
    const hasDateRangeInvalid = checkDateRangeIfValid();

    if (!hasOverlaps && !hasNoStartDates && !hasDateRangeInvalid) {
      cancelButton();
      const titleList = selectedProgrammeTitleList.map((title) => {
        const normalEffectivityDates = title.effectivityDates.filter((date) => {
          if (date.startDate) return date;
        });
        return ({
          ...title,
          effectivityDates: normalEffectivityDates
        })
      });
      onAddTitles(titleList);
    }
  };

  useEffect(() => {
    if(show) setVisible(true);
  }, [show])

  return (
    <div>
    {visible && (
    <Dialog width={766} height={564} onClose={toggleDialog}>
      <div className={stylesModal.dialogBody}>
        <div className={stylesModal.titleDialog}>Title Exclusion Dates</div>
        <div className={stylesModal.description}>
          Please specify the date range which you want to exclude the following
          program titles:
        </div>

        <div className={stylesModal.titleScope}>
          {selectedProgrammeTitleList.map((item, index) => (
            <div key={item.titleId} className={`${stylesModal.titleList} d-flex`} style={{marginTop: index > 0 ? "10px" : "38px"}}>
              <div className={stylesModal.titleListColumn1}>
                <div className={stylesModal.title}>{item.titleName}</div>
              </div>

              <div className={stylesModal.titleListColumn2}>
                {item.effectivityDates.map((item2, index2) => {
                  const isDateOverlapping = overlappingDates.includes(`${item.titleId}-${item2.id}`);
                  const dateRangeIsInvalid = invalidDateRanges.includes(`${item.titleId}-${item2.id}`);
                  const isDateLessThanToday = lessThanTodayDates.includes(`${item.titleId}-${item2.id}`);
                  const hasNoStartDate = hasNoStartDates.includes(item.titleId);
                  const value =  { start: item2.startDate, end: item2.endDate };     

                  return (
                    <div>
                        <div className={`${stylesModal.effectivityDatesSection} d-flex`} style={{paddingBottom: index === 0  && index2 === 0 ? "0px" : "5px"}}>
                          <div className={stylesModal.effectivityDatesDiv}>
                              <CustomDateRangeInput
                                key={item2.id}
                                min={min}
                                onChange={(e) => handleChange(e, index, index2, item2.historicalStartDate)}
                                startSettings={{
                                    value: value.start && new Date(value.start),
                                    labelLeft: true,
                                    disabled: item2.historicalStartDate,
                                    dayStart: item2.dayStart,
                                    monthStart: item2.monthStart,
                                    yearStart: item2.yearStart,
                                }}
                                endSettings={{
                                    value: value.end && new Date(value.end),
                                    labelLeft: true,
                                    disabled: item2.historicalEndDate,
                                    dayEnd: item2.dayEnd,
                                    monthEnd: item2.monthEnd,
                                    yearEnd: item2.yearEnd,
                                }}
                            />
                          </div>

                          <div class={stylesModal.iconDiv}>
                            {isDateComponent &&
                            subIndex === index2 &&
                            titleIndex === index ? (
                              <>
                                <div ref={dateFilterRef}>
                                  <TitleExclusionDateFilterComponent
                                    backDropClick={setisDateComponent}
                                    onSelectDateStart={(date) => selectDateStart(date)}
                                    onSelectDateEnd={(date) => selectDateEnd(date)}
                                  />
                                </div>
                              </>
                            ) : (
                              <>
                                <div></div>
                              </>
                            )}
                            {index2 === 0 ? (
                              <>
                                <img                              
                                  src={AddButton}
                                  className={stylesModal.addIcon}
                                  onClick={() => addDateRange(index, index2)}
                                />
                              </>
                            ) : (
                              <img
                                src={TrashCan}
                                className={stylesModal.trashIcon}
                                onClick={() => deleteDateRange(index, index2)}
                              />
                            )}
                          </div>
                        </div>
                      {isDateOverlapping && (
                        <div className={stylesModal.errorText}>Overlapping date range for the same title is not allowed</div>
                      )}
                      {dateRangeIsInvalid && (
                        <div className={stylesModal.errorText}>Date range is invalid</div>
                      )}
                      {isDateLessThanToday && (
                        <div className={stylesModal.errorText}>Start should not be earlier than the current date</div>
                      )}
                      {hasNoStartDate && index2 === 0 && (
                        <div className={stylesModal.errorText}>Start date is required</div>
                      )}
                      {index2 === 0 && index === 0 && (
                        <>
                          <div className={stylesModal.checkButton}>
                            <Checkbox label="Use the same date range for all" active={isActive} onCheck={activateCheckbox} />                            
                          </div>
                        </>
                      )}
                    </div>
                  )
                })}
              </div>
            </div>
          ))}
          </div>
        <div className={stylesModal.buttons}>
          <Button defaultBtn text="Cancel" onClick={cancelButton} />
          <Button
            text={formType === 'Update' ? 'Done' : 'Exclude Titles'}
            onClick={handleOnExcludeTitles}
          />
        </div>
      </div>
    </Dialog>
    )}
    </div>
  );
}
ShowSetTitleExclusionDateModalComponent.propTypes = {};

ShowSetTitleExclusionDateModalComponent.defaultProps = {
  show: false,
};
ShowSetTitleExclusionDateModalComponent.displayName =
  "ImportBusinessRulesModalComponent";

export default ShowSetTitleExclusionDateModalComponent;
